      h2 1.6 Normativa de movilizacin de cargas

    p.mb-4 En Colombia se cuenta con una amplia normatividad para el transporte de carga, dichas normas contienen las obligaciones a que estn sujetas todas las empresas que prestan el servicio de carga en el territorio nacional.
    p.mb-4 Dicha normativa es: 

    .row.mb-4
      .col-lg-4.BG12.p-4(data-aos="fade-right")
        figure.mb-4  
          img.w-25(src='@/assets/curso/tema1/img28.svg')
        p Ley 105 de 1993, Art. 3, en la que se define el transporte pblico como una industria que debe garantizar la movilizacin de personas, cosas y objetos, haciendo uso de vehculos apropiados segn las necesidades de los sectores, el servicio que se presta de transporte pblico debe ser bajo condiciones de calidad y seguridad, recibiendo a cambio un pago o contraprestacin econmica (Agencia Nacional de Infraestructura, 1993).
      .col-lg-4.BG13.p-4(data-aos="flip-up")
        figure.mb-4  
          img.w-25(src='@/assets/curso/tema1/img29.svg')
        p Ley 336 de 1966, esta ley unifica los criterios para reglamentar y regular el transporte pblico en Colombia, tanto el transporte areo, frreo, martimo, masivo, fluvial y terrestre, as como la operacin dentro del territorio nacional (Funcin Pblica, 1996).
      .col-lg-4.BG14.p-4(data-aos="fade-left")
        figure.mb-4  
          img.w-25(src='@/assets/curso/tema1/img29.svg')
        p Resolucin 004100 del 28 de diciembre de 2004, esta resolucin reglamenta la tipologa de los vehculos automotores dispuestos para la carga de transporte terrestre, tambin regula los requisitos relacionados con las dimensiones, pesos mximos por vehculos y por ejes, la operacin en la red vial en todo el territorio nacional segn definicin, designacin y clasificacin registradas en la norma tcnica colombiana NTC 4788 donde se encuentra la tipologa para los vehculos de transporte de carga terrestre.

